<template>
  <div id="app" >
    <h1>在线翻译</h1>
    <h5 class="text-muted" style="margin-top: 10px">简单 / 够用 / 便捷</h5>
		<div class="main-content">
    <translateForm v-on:formSubmit="translateText"></translateForm>
    <translateOutput v-text="translatedText"></translateOutput>
		</div>
  </div>
</template>

<script>

import TranslateForm from './components/TranslateForm'
import TranslateOutput from './components/TranslateOutput'

export default {
  name: 'App',
  data:function(){
    return{
      translatedText:""
    }
  },
  components: {
    TranslateForm,TranslateOutput
  },
  methods:{
    translateText:function(text,language){
      // alert(text);
			this.$http.get('https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20191105T074201Z.51472898eeb8ce16.4195c07283da5229e27d16320c3904ad5c49c645&lang='+language+'&text='+text)
.then((response)=>{
          // console.log(response.body.text[0]);
          this.translatedText = response.body.text[0];
        })
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  /*color: #2c3e50;*/
  /*background: #F8F8FF;*/
  margin-top: 30px;
}
